import React, {Component} from 'react';
class Block extends Component {
  getImage() {
    const weatherCode = this.props.data.weather[0].id;
     const time = new Date(this.props.data.dt_txt).getHours();
     const timeOfDay = (time > 7 && time < 18) ? 'day' : 'night';
     const className = `weather-icon wi wi-owm-${timeOfDay}-${weatherCode}`;
     return className;
  }
  getDate() {
    var options = {
        year: "numeric", month: "short",
        day: "numeric", weekday: "short"
    };
     return new Date(this.props.data.dt_txt).toLocaleDateString("ru-ru", options);
  }
  getTime() {
    const hours = new Date(this.props.data.dt_txt).getHours();
    const minutes = new Date(this.props.data.dt_txt).getMinutes();
    if (minutes < 10 ) {
      return hours +":0"+minutes;
    }
    else {
      return hours+':'+minutes;
    }
  }
  render() {
    const {data} = this.props;
    return (<div className="Block">
    <h4>: {this.getDate()}</h4>
<h3>: {this.getTime()}</h3>
    <i className={this.getImage()} />
      <h1>{Math.round(data.main.temp)}C</h1>
      <h2>{data.weather[0].description}</h2>

  </div>);
  }
}
export default Block;



