import React, {Component} from 'react';
import Block from './Block';
import {Link} from 'react-router-dom';
var dataW,
  lat,
  long;
class Weather extends Component {
  constructor(props) {
    super(props);
    this.state = {
      fetching: true,
      ready: false,
      error: ""
    }
  }
  componentDidMount() {
    if (this.props.match.params.loc === 'auto') {
      this.getLocation();
    } else {
      this.fetchWeatherData(this.props.match.params.zip, this.props.match.params.cdc)
    }
  }
  weatherByCords(position) {
    const baseUrl = `http://api.openweathermap.org`;
    const path = `/data/2.5/forecast`;
    const appId = `1be9b7ff7dd72545ef99c25f2f32dcdd`;
    const query = `units=metric&lang=ru&appid=${appId}`;

    fetch(`${baseUrl}${path}?lat=${position.coords.latitude}&lon=${position.coords.longitude}&${query}`).then(response => response.json()).then(data => {
      dataW = data;
      console.log(data);
      if (dataW.cod !== "200") {
        console.log('err')
        this.setState({error: data.message, fetching: true})
      } else {
        this.setState({
          fetching: false
        });
      }

    }).catch(error => console.error(error));
  }
  getLocation() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(this.weatherByCords.bind(this));
    } else {}
  }
  fetchWeatherData = (city, country_code) => {
    const baseUrl = `http://api.openweathermap.org`;
    const path = `/data/2.5/forecast`;
    const appId = `1be9b7ff7dd72545ef99c25f2f32dcdd`;
    const query = `units=metric&lang=ru&appid=${appId}`;

    fetch(`${baseUrl}${path}?zip=${city},${country_code}&${query}`).then(response => response.json()).then(data => {
      dataW = data;
      if (dataW.cod !== "200") {
        console.log('err')
        this.setState({error: data.message, fetching: true})
      } else {
        this.setState({
          fetching: !this.state.fetching
        });
      }

    }).catch(error => console.error(error));
  }
  fetchIP = () => {
    fetch('//ip-api.com/json').then(response => response.json()).then(({zip, countryCode}) => this.fetchWeatherData(zip, countryCode)).catch(error => console.log(error));
  }
  render() {

    if (this.state.fetching === false) {
      if (this.props.match.params.view === '24') {
        return (<div id="main">
          <div id="cityNameBlock">
            <span>
              <Link to="/"> Back</Link>
            </span>
            <h1>  {dataW.city.name}</h1>
          </div>
          <div id="mainW"><Block data={dataW.list[1]}/>
            <Block data={dataW.list[3]}/>
            <Block data={dataW.list[5]}/>
            <Block data={dataW.list[7]}/>
            <Block data={dataW.list[9]}/>
          </div>
        </div>);
      }
      if (this.props.match.params.view === '12') {
        return (<div id="main">
          <div id="cityNameBlock">
            <span>
              <Link to="/"> Back</Link>
            </span>
            <h1>  {dataW.city.name}</h1>
          </div>
          <div id="mainW"><Block data={dataW.list[1]}/>
            <Block data={dataW.list[2]}/>
            <Block data={dataW.list[3]}/>
            <Block data={dataW.list[4]}/>
            <Block data={dataW.list[5]}/>
          </div>
        </div>);
      }
      if (this.props.match.params.view === '5') {
        return (<div id="main">
          <div id="cityNameBlock">
            <span>
              <Link to="/"> Back</Link>
            </span>
            <h1>  {dataW.city.name}</h1>
          </div>
          <div id="mainW"><Block data={dataW.list[1]}/>
            <Block data={dataW.list[9]}/>
            <Block data={dataW.list[17]}/>
            <Block data={dataW.list[25]}/>
            <Block data={dataW.list[33]}/>
          </div>
        </div>);
      }
    }
    if (this.state.error !== "") {
      return (<h1>{this.state.error}</h1>)
    } else {
      return (<h1>Loading ...</h1>);
    }
  }
}
export default Weather;



