import React, {Component} from 'react';
import $ from 'jquery';
var location;
class Home extends Component {

  handleClick() {
    let radios = document.getElementsByName('location');
    for (let i = 0, length = radios.length; i < length; i++) {
      if (radios[i].checked) {
        location = radios[i].value;
        break;
      }
    }
    if (location === 'man') {
      $('.manualLoc').removeClass('dn');
    }
    if (location === 'auto') {
      $('.manualLoc').addClass('dn');
    }
  }
  componentDidMount() {
    location = 'auto';
    $('select').each(function(){
    var $this = $(this), numberOfOptions = $(this).children('option').length;

    $this.addClass('select-hidden');
    $this.wrap('<div class="select"></div>');
    $this.after('<div class="select-styled"></div>');

    var $styledSelect = $this.next('div.select-styled');
    $styledSelect.text($this.children('option').eq(0).text());

    var $list = $('<ul />', {
        'class': 'select-options'
    }).insertAfter($styledSelect);

    for (var i = 0; i < numberOfOptions; i++) {
        $('<li />', {
            text: $this.children('option').eq(i).text(),
            rel: $this.children('option').eq(i).val()
        }).appendTo($list);
    }

    var $listItems = $list.children('li');

    $styledSelect.click(function(e) {
        e.stopPropagation();
        $('div.select-styled.active').not(this).each(function(){
            $(this).removeClass('active').next('ul.select-options').hide();
        });
        $(this).toggleClass('active').next('ul.select-options').toggle();
    });

    $listItems.click(function(e) {
        e.stopPropagation();
        $styledSelect.text($(this).text()).removeClass('active');
        $this.val($(this).attr('rel'));
        $list.hide();
    });

    $(document).click(function() {
        $styledSelect.removeClass('active');
        $list.hide();
    });

});
  }
  handleSubmit(e) {
    let view = document.getElementById('car').value;
    if (location === "auto") {
      this.props.history.push('weather/auto/'+view);
    }
    if (location === "man") {
      let zip = document.getElementById('zipcode').value;

      let country_code = document.getElementById('country').value;
      this.props.history.push('weather/man/'+view+'/'+ + zip + '/' + country_code)
    }
  }
  constructor(props) {
    super(props);
    this.state = {

      resolve: true
    }
  }
  render() {
    return (<div className="Home">
    <h1>    </h1>
      <div className="LocationSelect">
        <div>
          <input type="radio" id="auto" name="location" value="auto" defaultChecked={true} onClick={(e) => this.handleClick(e)}/>
          <label htmlFor="auto" onClick={(e) => this.handleClick(e)}>Automatic</label>
          <input type="radio" id="man" name="location" value="man" onClick={(e) => this.handleClick(e)}/>
          <label htmlFor="man" onClick={(e) => this.handleClick(e)}>Manual</label>
        </div>

      </div>
      <div className="manualLoc dn">
        <input type="text" id="zipcode" placeholder="ZIP code"/>
        <input type="text" id="country" placeholder="County code"/>
      </div>
<h1>  :</h1>
  <select id="car">
    <option value="24">24 </option>
    <option value="12">12 </option>
    <option value="5">5 </option>
  </select>

<br/>

      <button className="" onClick={(e) => this.handleSubmit(e)}>Get Weather</button>
    </div>);
  }
}
Home.defaultProps = {
  check: 0
}
export {
  Home
};
export {
  location
};



